@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@200&display=swap);
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;

  }
  
header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  padding: 15px 5%;
  background-color: #B7D3F2;
}



.nav-links li{ 
  display: inline;
  list-style-type: none;
  text-decoration: none;
  margin: 10px;
}

.nav-links a { 
  text-decoration: none;
}

.logo { 
  display: inline ;
  font-size: 15px;
}



.hero { 
    display: -webkit-flex; 
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    height: 70vh;
    margin: 25px;
}

.hero-name { 
    font-size: 75px;
    margin-top: 1em; 
    margin-bottom: .5em; 
    /* margin: 1em auto; */
}

.hero-about-me { 
    font-size: 25px;
    margin: 0em 5em 2em 5em;
}

@media screen and (max-width: 310px) { 
    .hero-name { 
        font-size: 25px;
    }

    .hero-about-me { 
        font-size: 15px;
    }
}

@media screen and (max-width: 450px) { 
    .hero-name { 
        font-size: 25px;
    }
    .hero-about-me { 
        font-size: 20px;
        margin: 0em 0em 2em 0em;
    }
} 


@media screen and (max-width: 579px) { 
    .hero-name { 
        font-size: 25px;
    }

    .hero-about-me { 
        font-size: 20px;
        margin: 0em 0em 2em 0em;
    }
}

@media screen and (max-width: 710px) { 
    .hero-name { 
        font-size: 25px;
    }

    .hero-about-me { 
        font-size: 20px;
        margin: 0em 0em 2em 0em;
    }
}


.card { 
    display: -webkit-flex; 
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: row;
            flex-direction: row;
    margin: 20px;
}

.project-desc { 
     display: -webkit-flex; 
     display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding: 2px 16px;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 45vw;
    max-width: 560px;
    margin: 10px;
}

.title { 
    margin-bottom: .5em;
    font-size: 25px;
}

.btn-container { 
    display: block;
}

.projec-btns { 
    width: 100px;
    height: 25px;
    border: none;
    margin: 0.5em;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    cursor: pointer;
}

.projec-btns:hover { 
    -webkit-transform: translateY(-3px); 
            transform: translateY(-3px);
    box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.4);
}

img { 
    width: 300px;
    height: 300px;
    margin:10px;  
    width: 45vw;
    max-width: 560px;
    height: auto;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
}

@media screen and (max-width: 750px) {
    .card {
        -webkit-flex-direction: column;
                flex-direction: column;
    }
    img { 
        width: 90vw;
        max-width: 400px;
        height: auto;
    }
    .project-desc { 
        width: 90vw;
        max-width: 400px;
        height: auto;
    }
}

@media screen and (max-width: 280px) {
    .btn-container { 
        display: -webkit-flex; 
        display: flex; 
        -webkit-justify-content: center; 
                justify-content: center;
        -webkit-align-items: center;
                align-items: center;

    }
    .projec-btns { 
      width: 50px;
    }
}
.footer-container { 
    background: #B7D3F2;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    height: 12vh;
}

.socials 
.copyright { 
    margin: 5px;
}

.socials li { 
    display: inline;
    list-style-type: none;
    text-decoration: none;
    margin: 20px;
    background-color: #B7D3F2;
}

.socials button { 
    background-color: #B7D3F2;
    border: none;
    cursor:pointer;
    font-size: 19px;
    margin-bottom: 5px;
    margin-top: 5px;
}


